// 导入组件
import Vue from 'vue';
import Router from 'vue-router';
// 登录
import login from '@/views/login';
// 首页
import index from '@/views/index';
/**
 * 基础菜单
 */
// 商品管理
import Goods from '@/views/goods/Goods';
// 新建流程
import CreatePro from '@/views/process/CreatePro';
// 我的请求
import MyAsk from '@/views/process/MyAsk';
/**
 * 订单管理
 */
// 交易订单
import Order from '@/views/pay/Order';
/**
 * 系统管理
 */
// 用户管理
import user from '@/views/system/user';
// 角色管理
import Role from '@/views/system/Role';
// 公司管理
import Dept from '@/views/system/Dept';
//岗位管理
import Station from '@/views/system/Station';
// 权限管理
import Permission from '@/views/system/Permission';

//折叠表格
import NodeType from '@/views/system/NodeType';


// 启用路由
Vue.use(Router);

// 导出路由 
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/login',
        name: '登录',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/index',
        name: '首页',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/process/todo',
            name: '待办事宜',
            component: Goods,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/new',
            name: '新建流程',
            component: CreatePro,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/myask',
            name: '我的请求',
            component: MyAsk,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/find',
            name: '查询流程',
            component: Order,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/haddo',
            name: '已办事宜',
            component: Order,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/user',
            name: '用户管理',
            component: user,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Role',
            name: '角色管理',
            component: Role,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Dept',
            name: '部门管理',
            component: Dept,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Station',
            name: '职位管理',
            component: Station,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/NodeType',
            name: '节点类型配置',
            component: NodeType,
            meta: {
                requireAuth: true
            }
        },  {
            path: '/system/Permission',
            name: '权限管理',
            component: Permission,
            meta: {
                requireAuth: true
            }
        }]
    }]
})


