/**
 * 新建流程
 */
 <template>
  <div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-row type="flex" >
            <el-col :span="12">
                <el-form-item label="流程类型">
                  <el-select v-model="form.typeId" clearable placeholder="请选择流程类型" @change="getTempList" @clear="clearTemp">
                    <el-option v-for="item in this.processTypeList" :label="item.typename" :value="item.id" :key="item.id"></el-option>
                  </el-select>
                </el-form-item>
            </el-col>
            <el-col>
                <el-form-item label="流程模板">
                  <el-select v-model="form.tempId" clearable placeholder="请选择流程模板" ref="tempData">
                    <el-option v-for="item in this.processeTempList" :label="item.name" :value="item.id" :key="item.id"></el-option>
                  </el-select>
                </el-form-item>
            </el-col>    
      </el-row>
       <el-form-item label="级别">
        <el-select v-model="form.level" clearable placeholder="请选择级别">
          <el-option label="正常" value="1"></el-option>
          <el-option label="重要" value="2"></el-option>
          <el-option label="紧急" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="节点类型">
        <el-select v-model="form.nodeTypeId" clearable placeholder="请选择节点类型">
          <el-option v-for="item in this.nodeTypeList" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
 

<script>
import { getProcessTypeList, getProcessTempList, getProcessNodeTypeBeginList } from "../../api/processMG";

export default {
  data() {
    return {
      form: {
        level: "",
        tempId: "",
        typeId: "",
        nodeTypeId: ""
      },
      //usertoken
      typeListParams: {
        usertoken: sessionStorage.getItem("logintoken")
      },
      //typeId
      tempListParams: {
        usertoken: sessionStorage.getItem("logintoken"),
        typeId: ''
      },
      nodeTypeListParams:{
        pageNum: 1,
        pageSize: 1000,
        usertoken: sessionStorage.getItem("logintoken")
      },
      //流程类型列表
      processTypeList: [],
      //流程模板列表
      processeTempList: [],
      //节点类型列表
      nodeTypeList: []
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    getTypeList() {
      getProcessTypeList(this.typeListParams)
        .then(res => {
          if (res.code == 200) {
            this.processTypeList = res.data;
          } else {
            this.$message.error(res.data);
          }
        })
        .catch(error => {
          this.$message.error(error);
        });
    },
    getTempList(){
      this.tempListParams.typeId = this.form.typeId;
      getProcessTempList(this.tempListParams).then( res => {
        if(res.code = 200){
          this.processeTempList = res.data;
        }else{
          this.$message.error(res.data);
        }
      }).catch( error => {
        this.$message.error(error);
      })
    },
    clearTemp(){
      this.form.tempId = "";
    },
    getNodeTypeList(){
      getProcessNodeTypeBeginList(this.nodeTypeListParams).then(res =>{
        if(res.code == 200){
          this.nodeTypeList = res.data.list;
        }else{
          this.$message.error(res.data);
        }
      }).catch(error => {
        this.$message.error("服务器异常！");
      })
    }
  },
  created() {
    //获取流程类型列表
    this.getTypeList();
    //获取节点类型列表
    this.getNodeTypeList();
  }
};
</script>


