/** 
* 节点类型配置
*/

<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>节点类型配置</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="选择流程类型：" >
        <el-select size="small" v-model="formInline.processTypeId" placeholder="请选择" clearable="">
          <el-option v-for="item in this.processTypeList" :label="item.typename" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleCreateBeginNode()">新增开始节点</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" @expand-change="expandChange" :data="nodeTypeData" highlight-current-row v-loading="loading"  element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="expand" width="50">
        <template slot-scope="props">
        <el-table  size="small" label-position="left" :data="nodeTypeListWithQueue" highlight-current-row v-loading="loading" element-loading-text="拼命加载中">
          <el-table-column align="center" sortable prop="name" label="名称" width="280">
          </el-table-column>
          <el-table-column align="center" sortable prop="correlationName" label="审核人姓名" width="200">
          </el-table-column>
          <el-table-column align="center" sortable prop="parentsName" label="父节点名称" width="380">
          </el-table-column>
          <el-table-column label="操作" min-width="200">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="deleteNodeType(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      </el-table-column>
      <el-table-column align="center" sortable prop="name" label="名称" width="280">
      </el-table-column>
      <el-table-column align="center" sortable prop="correlationName" label="审核人姓名" width="200">
      </el-table-column>
      <el-table-column align="center" sortable prop="parentsName" label="父节点名称" width="380">
      </el-table-column>
      <el-table-column label="操作" min-width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit(scope.$index, scope.row)">添加子节点</el-button>
          <el-button size="mini" type="danger" @click="deleteNodeType(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!-- 编辑界面 -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="120px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="选择流程类型：" required>
        <el-select size="small" v-model="editForm.processTypeId" placeholder="请选择">
          <el-option v-for="item in this.processTypeList" :label="item.typename" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
        <el-form-item label="节点类型名称" prop="name" required>
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder="请输入节点类型名称"></el-input>
        </el-form-item>
        <el-form-item label="是否为开始节点" prop="isBeginNode" required>
          <el-select size="small" v-model="editForm.isBeginNode" placeholder="请选择" class="userRole">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否为结束节点" prop="isEndNode" required>
          <el-select size="small" v-model="editForm.isEndNode" placeholder="请选择" class="userRole">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="父节点" prop="parentsId" v-if="editForm.isBeginNode == '0'" required>
          <el-select size="small" v-model="editForm.parentsId" multiple placeholder="请选择" class="userRole">
            <el-option v-for="item in nodeTypeListWithQueue" :label="item.name" :value="item.id" :key="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="当前节点处理人"  prop="correlationId">
          <el-select size="small" v-model="editForm.correlationId" placeholder="请选择" class="userRole">
            <el-option v-for="item in userData" :label="item.realName" :value="item.uid" :key="item.uid"></el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'>取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">保存</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// 导入请求方法
import { getProcessTypeList, getProcessNodeTypeBeginList ,getNodeTypeListByQueue, postNodeType} from '../../api/processMG'
import { userList } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      title: '添加节点类型',
      editFormVisible: false, //控制编辑页面显示与隐藏
      dataAccessshow: false, //控制数据权限显示与隐藏
      unitAccessshow: false, //控制所属单位隐藏与显示
      // 编辑与添加
      editForm: {
        id: '',
        name: '',
        parentsId: '',
        isBeginNode: '1',
        isEndNode: '',
        correlationId: '',
        processTypeId: '',
        nodeTypeQueue: '',
        parentsName: '',
        correlationName: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      //删除
       deleteForm: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules表单验证
      rules: {
        name: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        processTypeId: [{ required: true, message: '请选择节点类型', trigger: 'blur' }],
        isBeginNode: [{ required: true, message: '请选择是否为开始节点', trigger: 'blur' }],
        isEndNode: [{ required: true, message: '请选择是否为结束节点', trigger: 'blur' }],
        parentsId: [{ required: true, message: '请选择父节点', trigger: 'blur' }]
      },
      // 获取用户列表
      getUserParams: {
        pageNum: 1,
        pageSize: 10000,
        usertoken: sessionStorage.getItem('logintoken'),
        state: '1'
      },
      //usertoken 
      usertokenParamer: {
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 请求数据参数
      formInline: {
        pageNum: 1,
        pageSize: 10,
        usertoken: sessionStorage.getItem('logintoken'),
        processTypeId: ''
      },
      //请求同一个queue数据的参数
      formInlineWithQueue: {
        usertoken: sessionStorage.getItem('logintoken'),
        nodeTypeQueue: ''
      },
      //用户数据列表
      userData: [],
      //基础节点类型数据
      nodeTypeData: [],
      //流程类型列表数据
      processTypeList: [],
      //根据queue获取的数据
      nodeTypeListWithQueue: [],
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },

  /**
   * 数据发生改变
   */
  watch: {},

  /**
   * 创建完毕
   */
  created() {
    this.getSelectNodeTypeList(this.usertokenParamer)
    this.getUser(this.getUserParams)
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取数据方法
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口，注释上面模拟数据 取消下面注释
       */
      //获取用户列表
      getProcessNodeTypeBeginList(parameter).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.nodeTypeData = res.data.list
          // 分页赋值
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    },
    //获取流程类型下拉列表
    getSelectNodeTypeList(paramer){
      getProcessTypeList(paramer).then(res => {
          if(res.code == 200){
            this.processTypeList = res.data;
          }else{
            this.$message.error(res.data);
          }
      })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    //搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      //获取流程类型列表
      this.getSelectNodeTypeList(this.usertokenParamer)
      if (row != undefined && row != 'undefined') {
        this.title = '添加节点类型信息'
        this.editForm.id = ""
        this.editForm.name = ""
        this.editForm.parentsId = row.parentsId
        this.editForm.parentsName = row.parentsName
        this.editForm.correlationId = row.correlationId
        this.editForm.correlationName = row.correlationName
        this.editForm.isBeginNode = ""
        this.editForm.isEndNode = ""
        this.editForm.processTypeId = row.processTypeId
        this.editForm.nodeTypeQueue = row.nodeTypeQueue
        this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue
        //获取流程类型列表
        this.getSelectNodeTypeList(this.usertokenParamer)
        getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
          if(res.code == 200){
            this.nodeTypeListWithQueue = res.data;
          }else{
            this.$message.error(res.data);
          }
        }).catch(err => {
          this.$message.error('服务器错误！')
        })
      } else {
        this.title = '添加节点类型信息'
         this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.parentsId = ''
        this.editForm.parentsName = ''
        this.editForm.correlationId = ''
        this.editForm.correlationName = ''
        this.editForm.isBeginNode = '1'
        this.editForm.isEndNode = ''
        this.editForm.processTypeId = ''
        this.editForm.nodeTypeQueue = ''
      }
    },
    //删除节点
    deleteNodeType: function(index,row){
      this.deleteForm.id = row.id;
      // 请求方法
      postNodeType(this.deleteForm)
        .then(res => {
          this.editFormVisible = false
          this.loading = false
          if (res.code == 200) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: '节点删除成功！'
            })
          } else {
            this.$message({
              type: 'info',
              message: res.data
            })
          }
        })
        .catch(err => {
          this.editFormVisible = false
          this.loading = false
          this.$message.error('服务器错误！')
        })
    },
    //新增开始节点
    handleCreateBeginNode: function(index, row) {
      this.editFormVisible = true
      //获取流程类型列表
      this.getSelectNodeTypeList(this.usertokenParamer)
      
      if (row != undefined && row != 'undefined') {
        this.title = '修改节点类型信息'
        this.editForm.id = row.id
        this.editForm.name = row.name
        this.editForm.parentsId = row.parentsId
        this.editForm.parentsName = row.parentsName
        this.editForm.correlationId = row.correlationId
        this.editForm.correlationName = row.correlationName
        this.editForm.isBeginNode = row.isBeginNode
        this.editForm.isEndNode = row.isEndNode
        this.editForm.processTypeId = row.processTypeId
        this.editForm.nodeTypeQueue = row.nodeTypeQueue
        this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue
        //获取流程类型列表
        this.getSelectNodeTypeList(this.usertokenParamer)
        getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
          if(res.code == 200){
            this.nodeTypeListWithQueue = res.data;
          }else{
            this.$message.error(res.data);
          }
        }).catch(err => {
          this.$message.error('服务器错误！')
        })
      } else {
        this.title = '添加节点类型信息'
         this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.parentsId = ''
        this.editForm.parentsName = ''
        this.editForm.correlationId = ''
        this.editForm.correlationName = ''
        this.editForm.isBeginNode = '1'
        this.editForm.isEndNode = ''
        this.editForm.processTypeId = ''
        this.editForm.nodeTypeQueue = ''
      }
    },
    // 编辑、添加提交方法
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          // 请求方法
          postNodeType(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: '数据保存成功！'
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('保存失败，请稍后再试！')
            })
        } else {
          return false
        }
      })
    },
    // 表格下拉事件
    expandChange(row,expandedRows) {
      this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue;
      getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
        if(res.code == 200){
          this.nodeTypeListWithQueue = res.data;
        }else{
          this.$message.error(res.data)
        }
      }).catch( error => {
        this.$message.error('服务器错误！');
      })
      
    },
    // 关闭编辑、增加弹出框
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.dataAccessshow = false
      } else if (dialog == 'unit') {
        this.unitAccessshow = false
      }
    },
    // 获取用户列表
    getUser(paramer) {
       //获取用户列表
      userList(paramer).then(res => {
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.userData = res.data.list
          // 分页赋值
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.userDept {
  width: 100%;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>
</style>

 


