/**
 * 系统管理 职位管理
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>职位管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="searchformInline" class="user-search">
      <el-form-item label="搜索">
        <el-input size="small" v-model="searchformInline.name" placeholder="输入职位名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" @selection-change="selectChange" :data="stationData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="index" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="部门" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="name" label="职位名称" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="uname" label="任职人员" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="createdtime" label="创建时间" width="150">
        <template slot-scope="scope">
            <div>{{scope.row.createdtime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteStation(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!-- 编辑界面 -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="职位名称" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder="请输入职位名称"></el-input>
        </el-form-item>
        <el-form-item label="任职人员" prop="uid">
            <el-select size="small" v-model="editForm.uid" placeholder="请选择" class="userDept">
                <el-option v-for="item in userselectList" :key="item.uid" :label="item.realName"  :value="item.uid"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="部门" prop="deptid">
          <el-select size="small" v-model="editForm.deptid" placeholder="请选择" class="userDept">
            <el-option v-for="item in deptList" :key="item.deptId" :label="item.deptName"  :value="item.deptId"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'>取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">保存</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// 导入请求方法
import { userList,getDeptList,getStationList,postStation } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      title: '添加职位信息',
      editFormVisible: false, //控制编辑页面显示与隐藏
      dataAccessshow: false, //控制数据权限显示与隐藏
      unitAccessshow: false, //控制所属单位隐藏与显示
      // 编辑与添加
      editForm: {
        id: '',
        uid: '',
        name: '',
        realName: '',
        deptid: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 选择数据
      selectdata: [],
      // rules表单验证
      rules: {
        name: [
          { required: true, message: '请输入职位名', trigger: 'blur' }
        ],
        uid: [
          { required: true, message: '请选择任职人姓名', trigger: 'blur' }
        ],
        deptid: [{ required: true, message: '请选择部门', trigger: 'blur' }]
      },
      // 删除岗位
      seletedata: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      //获取部门 
      getdeptListparam: {
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 请求数据参数
      formInline: {
        pageNum: 1,
        pageSize: 10,
        usertoken: sessionStorage.getItem('logintoken'),
        name: ''
      },
      //搜索数据参数
      searchformInline: {
        pageNum: 1,
        pageSize: 1000,
        usertoken: sessionStorage.getItem('logintoken'),
        name: ''
      },
      //请求用户数据
      userListParams: {
          pageNum : 1,
          pageSize : 1000,
          usertoken: sessionStorage.getItem('logintoken')
      },
      //用户数据
      userselectList: [],
      //岗位数据
      stationData: [],
      //部门数据
      deptList: [],
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
    this.getDeptSelectList(this.getdeptListparam)
    this.getUserList()
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取数据方法
    getdata(parameter) {
      this.loading = true
      getStationList(parameter).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.stationData = res.data.list
          // 分页赋值
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    },
    //获取部门下拉列表
    getDeptSelectList(paramer){
      getDeptList(paramer).then(res => {
          if(res.code == 200){
            this.deptList = res.data;
          }else{
            this.$message.error(res.data);
          }
      })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    //搜索事件
    search() {
      this.getdata(this.searchformInline)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = '修改岗位信息'
        this.editForm.id = row.id
        this.editForm.uid = row.uid
        this.editForm.name = row.name
        this.editForm.deptid = row.deptid
      } else {
        this.title = '添加岗位'
        this.editForm.id = ''
        this.editForm.uid = ''
        this.editForm.name = ''
        this.editForm.deptid = ''
      }
    },
    // 编辑、添加提交方法
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          // 请求方法
          postStation(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: '数据保存成功！'
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('保存失败，请稍后再试！')
            })
        } else {
          return false
        }
      })
    },
    // 选择复选框事件
    selectChange(val) {
      this.selectdata = val
    },
    // 关闭编辑、增加弹出框
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.dataAccessshow = false
      } else if (dialog == 'unit') {
        this.unitAccessshow = false
      }
    },
    // 删除
    deleteStation(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
    }).then(() => {
        // 删除
        this.seletedata.id = row.id;
        postStation(this.seletedata)
        .then(res => {
            if (res.code == 200) {
            this.$message({
                type: 'success',
                message: '数据已删除!'
            })
            this.getdata(this.formInline)
            } else {
            this.$message({
                type: 'info',
                message: res.data
            })
            }
        }).catch(err => {
            this.loading = false
            this.$message.error('数据删除失败，请稍后再试！')
        })
    }).catch(() => {
        this.$message({
            type: 'info',
            message: '已取消删除！'
        })
    })},
    //获取用户列表
    getUserList(){
         //获取用户列表
      userList(this.userListParams).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.userselectList = res.data.list
        }
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.userDept {
  width: 100%;
}
</style>

 


