import axios from 'axios';
import { req, axiosGet } from './axiosFun';

/**
 * 流程管理
 */
//获取流程类型列表
export const getProcessTypeList = (params) =>{ return axiosGet("/api/getProcessTypeList", params)};
//通过流程获取流程模板列表
export const getProcessTempList = (params) =>{ return axiosGet("/api/getProcessTempList", params)};
//根据typeId 、 isBegin = 1 获取流程节点类型列表
export const getProcessNodeTypeBeginList = (params) => { return axiosGet("/api/getProcessNodeTypeBeginList",params)};
//根据nodeTypeQueue获取 nodeTypeList
export const getNodeTypeListByQueue = (params) => { return axiosGet("/api/getNodeTypeListByQueue", params)};
//配置流程节点类型
export const postNodeType = (params) => { return req('post','/api/postNodeType',params)};


